.NavBarContainer{
    width: 100%;
    height: 100%;
    background-color: #040b14;
    display: flex;
    display: flex;
    flex-direction: column;
}

/* .open{
    left:0
} */

.Profile{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center ;
    height: auto;
    width: 100%;
    border-bottom: 2px solid #2c2f3f;
}

.ProfilePic{
    border: 8px solid #2c2f3f;
    width: 100%;
    border-radius: 50%;
    aspect-ratio: 1;
    width: 8rem;
    margin-top: 1rem;
}

.TextName{
    font-weight: 600;
    color: white;
    font-size: 24px;
    margin: 0;
    text-align: center;
    font-family: "Poppins", sans-serif;
    margin-top: 0.5rem;
}

a{
    text-decoration: none;
    color: white
}

.SocialMediaContainer{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    flex-wrap: wrap; /* Allow items to wrap when necessary */
}

.SocialTag{
    aspect-ratio: 1;
    height: 36px;
    border-radius: 50%;
    background-color: #2c2f3f;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}

.SocialTag:hover{
    background-color: #149ddd;
}

.NavBarMenu{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: transparent;

    overflow: hidden; 
}

.NavBarMenu > .ant-menu-item{
    max-width: 95%;

    white-space: nowrap; /* Prevent text from wrapping */
    overflow: hidden; /* Hide overflowing text */
    text-overflow: ellipsis; /* Add ellipsis (...) for truncated text */


    height: fit-content;
    margin: 5px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 15px;
    color: grey;
    display: flex;
    flex-direction: row;
    padding-left: 15px;
    align-items: center;
    font-size: 1.5rem;
    /* Added transition property - helps smooth out on hover */
    transition: background-color 0.3s ease-in, padding 0.3s ease, color 0.3s ease;
}

.NavBarMenu > .ant-menu-item:hover{
    color: white !important;
    padding-top:30px;
    padding-bottom: 30px;
    border: 2px solid white ;
}

.NavBarMenu > .ant-menu-item-selected{
    color: white;
}